.game-login {
    padding: 20px;
    padding-top: 0px;
}

.game-login h1 {
    font-family: sans-serif;
    color: gray;
    font-weight: bold;
    margin-bottom: 40px;
}

.game-login .col {
    margin-right: 40px;
}

.game-login label {
    text-align: right;
    width: auto;
}

.game-login input {
    max-width: 300px;
    min-width: 300px;
    border-color: #69c521;
}

.game-login-top {
    text-align: right;
}

.game-login-top>div>span {
    color: #69c521;
    font-family: sans-serif;
    font-size: 18px;
}

.game-login-form {
    margin-top: 20px;
}

@media only screen and (max-width: 680px) {
    .game-login h1 {
        margin-bottom: 25px;
    }
    .game-login .col {
        margin-right: 10px;
        margin-bottom: 10px;
        text-align: center;
    }
    .game-login label {
        display: none;
    }
    .game-login input {
        display: unset;
        max-width: 200px;
        min-width: auto;
    }
}